.topBar {
    margin: 0%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px); /* for Safari */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 7vh;
    z-index: 9999;

    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.topBar h1 {
    margin: 1.5vw;
    font-size: 2vw;
}

.topBar .navigationButtons {
    display: flex;
    align-items: center;
    gap: 5px;
    position: relative;
    margin: 15px;
}

.topBarBackground {
    position: static;
    top: 0;
    left: 0;
    width: 100%;
    height: 7vh;
    background-color: black;
    z-index: 2;
}

.topBar .pageNameHeading {
    text-decoration: none;
    color: inherit;
}

/* Responsive navbar tweaks */
@media (max-width: 800px){
    .topBar{
        flex-direction: column;
        align-items: stretch;
        height: auto;
        padding: 8px 12px;
        gap: 6px;
    }
    .topBar h1{
        margin: 0;
        font-size: 5vw;
        padding-left: 6px;
    }
    .topBar .navigationButtons{
        justify-content: center;
        flex-wrap: wrap;
        margin: 0 6px 6px 6px;
        gap: 8px;
    }
}

@media (max-width: 420px){
    .topBar h1{font-size:6.5vw}
}

/* Menu button (hidden on wide screens) */
.menuButton{
    display:none;
    background: transparent;
    border: none;
    padding: 6px;
    margin-right: 12px;
    cursor: pointer;
}
.menuButton img{width:28px;height:28px}

/* Sidebar overlay and panel */
.sidebarOverlay{
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,0.45);
    z-index: 9998;
}

.sidebar{
    position: fixed;
    top: 0;
    right: -320px;
    width: 300px;
    height: 100vh;
    background: var(--primary-bg-color);
    box-shadow: -8px 0 24px rgba(0,0,0,0.4);
    z-index: 9999;
    display: flex;
    flex-direction: column;
    transition: right 0.28s ease-in-out;
    padding: 12px;
}
.sidebar.open{right:0}
.sidebarHeader{display:flex;align-items:center;justify-content:space-between;padding:8px 4px;border-bottom:1px solid rgba(255,255,255,0.05)}
.sidebarHeader h2{margin:0;font-size:1.1rem}
.closeButton{background:transparent;border:none;cursor:pointer;padding:6px}
.closeButton img{width:28px;height:28px}
.sidebarButtons{display:flex;flex-direction:column;gap:10px;padding:12px}
.sidebarLink{color:var(--primary-text-color);text-decoration:none;padding:10px 14px;border-radius:8px;background:transparent}
.sidebarLink:hover{background:rgba(255,255,255,0.03)}

/* Responsive behavior: hide nav buttons and show menu button when small */
@media (max-width: 800px){
    .navigationButtons{display:none !important}
    .menuButton{display:block;align-self:flex-end}
}

@media (max-width:420px){
    .sidebar{width:260px}
    .sidebar.open{right:0}
    .menuButton img{width:26px;height:26px}
    .closeButton img{width:26px;height:26px}
}