.project-card {
    background-color: var(--primary-bg-color);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 8px;
    padding: 16px;
    width: 25%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 25px;
}

.project-card .infoContainer {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.project-card .heading {
    text-align: center;
}

.project-card .descriptionContainer {
    text-align: center;
    font-size: 1rem;
}

.project-card .tagsContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: center;
}

.project-card .tagsContainer .tag {
    background-color: var(--secondary-bg-color);
    color: var(--primary-text-color);
    padding: 4px 8px;
    border-radius: 5px;
    font-size: 0.85rem;
}

.project-card .linkContainer {
    display: flex;
    justify-content: center;
}

/* Responsive card sizing */
@media (max-width: 1000px){
    .project-card{width:48%;min-height:unset}
}

@media (max-width: 600px){
    .project-card{width:100%;padding:14px}
    .project-card .descriptionContainer{font-size:0.95rem}
    .project-card .tagsContainer .tag{font-size:0.8rem}
}
