.basic-button {
    color: black;
    text-decoration: none;
    background-color: white;
    font-family: var(--primary-text-font);
    padding: 0.5rem 0.9rem;
    border-radius: 10px;
    font-size: 1rem;
    width: fit-content;
    height: fit-content;
}

.basic-button {
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.basic-button:hover {
    transition: opacity 0.3s ease-in-out;
    opacity: 0.8;
}

@media (max-width:600px){
    .basic-button{padding:0.45rem 0.75rem;font-size:0.95rem}
}
