.navbar-button {
    color: var(--primary-text-color);
    text-decoration: none;
    font-family: var(--primary-text-font);
    padding: 0.6rem 0.9rem;
    border-radius: 10px;
    min-width: 64px;
    width: auto;
    font-size: 1rem;
}

.navbar-button {
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.navbar-button:hover {
    transition: opacity 0.3s ease-in-out;
    opacity: 0.8;
}

@media (max-width: 800px){
    .navbar-button{
        padding: 0.55rem 0.8rem;
        font-size: 0.95rem;
    }
}

@media (max-width:420px){
    .navbar-button{font-size:0.9rem;padding:0.45rem 0.7rem}
}