body {
    background-color: var(--secondary-bg-color);
    margin: 0;
    padding: 0;
    color: white;
    font-family: Helvetica, sans-serif;
    
    min-height: 100vh;
    display: grid;
    grid-template-rows: auto 1fr auto;
}

.pageContent {
    padding: 10px 50px 0px 50px;
}

.AppContainer {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

/* Responsive utilities */
:root{
    --max-content-width: 1100px;
}

.container{
    width: 100%;
    max-width: var(--max-content-width);
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
}

/* Make page content padding fluid and comfortable on small screens */
.pageContent{
    padding-top: 12px;
    padding-bottom: 24px;
}

/* Typography scaling */
html{font-size:16px}
@media (max-width: 600px){
    html{font-size:15px}
}
@media (max-width: 400px){
    html{font-size:14px}
}

/* General responsive layout tweaks */
@media (max-width: 900px){
    .pageContent{padding-left:16px;padding-right:16px}
}
