.footerBar {
    margin: 0%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    position: static;
    top: 0;
    left: 0;
    width: 100%;
    height: 75px;
    z-index: 9999;

    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
    background-color: var(--primary-bg-color);
}

.footerBar .navigationButtons {
    display: flex;
    align-items: center;
    gap: 5px;
    position: relative;
    margin-right: 5vw;
}

.footerBar p {
    margin-left: 5vw;
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.7);
}

@media (max-width:800px){
    .footerBar{flex-direction:column;align-items:center;gap:8px;padding:12px;height:auto}
    .footerBar p{margin:0;font-size:0.95rem}
    .footerBar .navigationButtons{margin:0}
}
